* {
  margin: 0;
  padding: 0;
  font-family: Wellfleet;
  text-decoration: none;
  color: black;
}

.About {
  background-color: whitesmoke;

}

.App {
  overflow-x: hidden;
  overflow-y: auto;
  scroll-behavior: smooth;
}

.cii {
  margin-bottom: .25cm;
}

.cimg-box {
  display: flex;
  align-items: center;
  column-gap: .2cm;
  margin-top: .1cm;
}

body {
  scroll-behavior: smooth;
}

.cimg {
  width: 2cm;
  height: auto;

}

.idiv {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: .2cm;
  border-radius: 10px;
  width: 2.2cm;
  height: 2.55cm;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border: 1px solid rgb(200, 200, 200);
}

.office {
  width: 1.3cm;
  height: auto;
}

.exp-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: .35cm;
  row-gap: .35cm;

}

.exp {
  display: flex;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  width: 11cm;
  border: 1px solid rgb(199, 199, 199);
  align-items: flex-start;
  padding: .3cm .35cm;
  border-radius: 10px;
  /* background-color: rgb(237, 214, 255); */
  background-color: white;
  column-gap: .2cm;
}

.year {

  font-size: 15px;
  color: rgb(97, 97, 97);
}

.type {
  margin-top: .22cm;
  font-size: 15px;
  color: rgb(97, 97, 97);
}

.place {
  color: rgb(97, 97, 97);
  font-size: 14px;
}

.role {
  font-size: 22px;
}

.cmpny-name {
  font-size: 17px;

}

.Home {
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  row-gap: .3cm;
  margin-top: 1cm;
  column-gap: .3cm;
  flex-wrap: wrap;
}

.hb {
  padding: .28cm .7cm;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  background-color: royalblue;
  color: white;
  cursor: pointer;
  transition: .2s;



}

.hb:hover {
  background-color: rgb(40, 87, 226);

}

.hbg {
  border: none;
  background-color: green;
  color: white;


}

.hbg:hover {
  background-color: rgb(2, 99, 2);

}

.home-box {
  display: flex;
  row-gap: .1cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 20cm;
  padding: .3cm .5cm;
  /* border: 1px solid white; */

}

.tagline {
  font-size: 20px;
  color: white;
  text-align: center;
  font-family: Wellfleet;

}

.name {
  font-size: 90px;
  font-family: Wellfleet;
  color: white;
  font-weight: 600;
  letter-spacing: .06cm;
}

.Home {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("me.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center center;
  filter: brightness(23%);
  /* Adjust the value as needed (0.7 reduces brightness) */
}

.About {

  padding: .2cm 10vw 2cm 10vw;

  /* To position the pseudo-element */
}





.about-box {
  display: flex;


  flex-direction: column;
  align-items: center;

}

.pro-dets {
  display: flex;
  flex-direction: column;
  row-gap: .4cm;
}

.heading {
  font-size: 35px;
  margin-bottom: 1.5cm;
}

.pro-para-head {
  font-size: 22px;
}

.about-dets-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  column-gap: .7cm;
  row-gap: .5cm;
  padding: 0cm .3cm .3cm .3cm;
}

.pro-para-box {
  display: flex;
  row-gap: .08cm;
  flex-direction: column;
}

.ppic {
  width: 7.5cm;
  height: auto;
  border-radius: 50%;
}

.exp-main {
  margin-top: 0cm;


}

.contact-main {
  margin-top: 0cm;


}

.ct-para {
  text-align: center;
  padding: 0cm 10vw;
}

.ct-buttons {
  padding: .5cm 0cm;
  display: flex;
  flex-wrap: wrap;
  column-gap: .3cm;
  row-gap: .3cm;
}

.ctb {
  text-decoration: none;
  color: black;
  padding: .22cm .85cm;
  border: 1px solid black;
  border-radius: 8px;
  border: 2.5px solid royalblue;
  transition: .3s;
  background-color: transparent;
  color: rgb(0, 0, 0);
  font-size: 18px;
  cursor: pointer;
}

.ctb:hover {
  background-color: royalblue;
  color: white;
}

.ct-main-box {
  margin-top: -1cm;
}


